/* ==========================================================================
   Design System
   Expandable Styling
   ========================================================================== */
.o-expandable {
  position: relative;
}
.o-expandable_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.625em 0.9375em;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}
.o-expandable_header:focus {
  outline: 1px dotted var(--black);
  outline-offset: 1px;
}
.o-expandable_header .o-expandable_cue-close,
.o-expandable_header .o-expandable_cue-open {
  display: none;
}
.o-expandable_header[aria-expanded='false'] .o-expandable_cue-open {
  display: block;
}
.o-expandable_header[aria-expanded='true'] .o-expandable_cue-close {
  display: block;
}
button.o-expandable_header {
  width: 100%;
  text-align: left;
}
.o-expandable_header .o-expandable_label {
  flex-grow: 1;
}
.o-expandable_label {
  margin-bottom: 0;
  color: var(--black);
  font-weight: 500;
}
.o-expandable_cues {
  min-width: 60px;
  text-align: right;
  color: var(--pacific);
  font-size: 1em;
  line-height: 1.375;
}
.o-expandable_content {
  padding: 0.9375em;
  padding-top: 0;
}
.o-expandable_content::before {
  content: '';
  display: block;
  border-top: 1px solid var(--gray-40);
  padding-top: 0.9375em;
}
.o-expandable_content::after {
  padding-bottom: 0.9375em;
  width: 100%;
}
.o-expandable__padded .o-expandable_label {
  padding: 0.9375em 0;
}
.o-expandable__background {
  background: var(--gray-5);
}
.o-expandable__border {
  border: 1px solid var(--gray-40);
}
.o-expandable-group .o-expandable {
  border-bottom: 1px solid var(--gray-40);
}
.o-expandable-group .o-expandable:first-child {
  border-top: 1px solid var(--gray-40);
}
@media print {
  .o-expandable_header[aria-expanded='true'] .o-expandable_cue-close,
  .o-expandable_header[aria-expanded='false'] .o-expandable_cue-open {
    display: none;
  }
  .o-expandable_content[aria-expanded='false'] {
    display: block;
    max-height: 99999px !important;
  }
}
.print .o-expandable_header[aria-expanded='true'] .print .o-expandable_cue-close,
.print .o-expandable_header[aria-expanded='false'] .print .o-expandable_cue-open {
  display: none;
}
.print .o-expandable_content[aria-expanded='false'] {
  display: block;
  max-height: 99999px !important;
}
html[lang='ar'] .o-expandable_header {
  text-align: right;
}
html[lang='ar'] .o-expandable_cues {
  text-align: left;
}
/* topdoc
  name: Summary
  family: cf-organisms
  notes:
    - "Styles a one-way Expandable-like organism that previews content."
  patterns:
  - name: Summary Mobile organism
    markup: |
      <div class="o-summary o-summary__mobile">
          <div class="o-summary_content">
               Content
          </div>
          <button class="o-summary_btn u-hidden">
            Read full description
          </button>
      </div>
    codenotes:
      - |
        Pattern structure
        -----------------
        .o-summary
          .o-summary_content
          .o-summary_btn.u-hidden

    notes:
      - "One-way expandable. Displays an approximately 4-line
         preview of content on mobile screen sizes only
         when __mobile modifier is used."
  tags:
  - cf-organisms
*/
.o-summary_content {
  overflow-y: hidden;
  padding: 2px;
  left: -2px;
  top: -2px;
  position: relative;
}
.o-summary_btn {
  position: relative;
  z-index: 2;
  display: block;
  width: 100%;
  padding-top: 15px;
  padding-bottom: 15px;
  border: dotted var(--pacific);
  border-width: 1px 0;
  text-align: center;
  color: var(--pacific);
  background: #fff;
}
.no-js .o-summary_btn {
  display: none;
}
.o-summary_btn:focus {
  outline: 1px dotted var(--pacific);
  outline-offset: 2px;
}
.o-summary_btn[aria-expanded='false']::before {
  display: block;
  pointer-events: none;
  height: 2.75em;
  margin: 0;
  position: absolute;
  left: 0;
  right: 0;
  top: calc(-100% + 4px);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #ffffff 100%);
  content: '';
}
@media print {
  .o-summary_btn[aria-expanded='false']::before {
    background: none;
  }
}
.print .o-summary_btn[aria-expanded='false']::before {
  background: none;
}
@media only screen and (min-width: 601px) {
  .o-summary__mobile .o-summary_btn {
    display: none;
  }
}
